import { CONSTANTS } from '../interface/index';
import { unSubscribeClassProperty } from './unSubscribeClassProperty';


/**
 * unsubscribe class observables/meta binding
 *
 */
export function unSubscribeClassMetaBinding(_class: any) {
    const META = _class.__proto__[CONSTANTS.META_BINDABLE];
    if (META) {
        const keys = Object.keys(META);
        for (const key of keys) {

            // save key
            const subscribeInternal = META[key].options.subscribeInternal;

            // if it exist unsubscribe
            if (subscribeInternal) {
                unSubscribeClassProperty(_class, subscribeInternal);

            }

            // get old
            const subscribeExternal = META[key].options.subscribeExternal;

            // if it exist unsubscribe
            if (_class.$parent && subscribeExternal) {
                if (_class.$element) {
                    const el = _class.$element;
                    const att = `${META[key].options.attribute}.bind`;
                    const attrValue = el.getAttribute(att);
                    if (attrValue) {
                        unSubscribeClassProperty(_class.$parent, subscribeExternal);
                    }
                }
            }

        }
    }
}
