// holds the value converters
const valueConverter = new Map();



/**
 * keeps track of the attributes, so I know when using the AST
 *
 */
export class ContainerValueConverters {



    /**
     * register new valueCOnverter
     *
     */
    public static regConverter(_class: any, name: string) {
        if (!valueConverter.has(name)) {
            valueConverter.set(name, _class);
        }
    }



    /**
     * returns converter function if found
     *
     */
    public static findConverter(name: string): any {
        if (valueConverter.has(name)) {
            return valueConverter.get(name);
        } else {
            return null;
        }
    }



}
