import { IElement, IControllerArray, INode } from '../interface/index';
import { ViewParser } from './viewParse';
import { ViewController } from './viewController';
import { ContainerClasses } from '../container/index';
import { BindingEngine } from '../binding/index';
import { Logger, Router } from '../utils/index';

/**
 * Element controller adds view and controls the lifecycle of element
 *
 */
export class ElementController {
    private element: INode;
    private _class: IElement;
    private parent: IElement;
    private templateString: string;
    private template: INode;
    private _process = true;
    private logger: Logger;


    constructor(parent: IElement, element: INode, _class: IElement, elementRef: any, templateString: string) {
        this.logger = Logger.getLogger((element as any).tagName, 'element');
        this.parent = parent;
        this.element = element;
        this.templateString = templateString;
        this._class = _class || ContainerClasses.get(elementRef);
        if (element && !element.$view) {
            // every element is a controller in it self, so create a new view
            element.$view = new ViewController(element);
            element.$view.addElement(this);
        } else {
            // debugger; // should not happen
        }
    }



    /**
     * start element life cycle
     *
     */
    public init(): IControllerArray {


        this._class.$element = this.element;
        this._class.$parent = this.parent;
        this._class.$attributes = this.element.attributes;

        // subscribe meta binding
        BindingEngine.subscribeClassMetaBinding(this._class);
        let arr: IControllerArray = [];

        if (this._class.loadTemplateAsync) {
            this._class.loadTemplateAsync().then((template: string) => {

                // if async template

                this.templateString = this.templateString || template;
                this.template = ViewParser.createTemplate(null, this.templateString);

                // router ?
                if (this._class.configureRouter) {
                    this.configureRouter();
                }

                // call create
                this.create();

                // process content
                this.processContent();

                // if process
                if (this._process) {
                    arr = ViewParser.parseNodes(this.template, this._class, this.element.$view);
                }

                // task is started, lets look children and append them, if we have process content
                if (this._process) {
                    while (this.template.childNodes.length) {
                        this.element.appendChild(this.template.firstChild);
                    }
                }
                arr.reverse();

                // todo, do I want to allow someone to stop appending the templates now?
                this.contentProcessed(arr);

                // need to attach them myself here
                arr.forEach((a) => {
                    if (a.attached) {
                        a.attached();
                    }
                });
            });
        } else {
            this.logger.log('loadTemplate');

            if (!this._class.loadTemplate) {
                throw new Error('loadTemplate missing');
            }
            this.templateString = this.templateString || this._class.loadTemplate();
            this.template = ViewParser.createTemplate(null, this.templateString);

            // router
            if (this._class.configureRouter) {
                this.configureRouter();
            }

            // call create
            this.create();

            // process content
            this.processContent();

            // if processed
            if (this._process) {
                arr = ViewParser.parseNodes(this.template, this._class, this.element.$view);
            }

            // task is started, lets look children and append them, if we have process content
            if (this._process) {
                while (this.template.childNodes.length) {
                    this.element.appendChild(this.template.firstChild);
                }
            }
            arr.reverse();

            // todo, do I want to allow someone to stop appending the templates now?
            this.contentProcessed(arr);
        }

        return arr;
    }



    /**
     * configure router
     *
     */
    public configureRouter(): void {
        this.logger.log('configureRouter');
        const router = ContainerClasses.get(Router);
        const RouterConfig = {
            root: '#/',
            title: '',
            map: (routes: any[]) => {
                (RouterConfig as any).routes = routes;
            },
            loader: ''
        };
        this._class.configureRouter(RouterConfig, router);
        router.addConfig(RouterConfig);
    }



    /**
     * sets vars and calls create on element
     *
     */
    public create(): void {
        this.logger.log('create');

        if (this._class.created) {
            this._class.created();
        }
    }



    /**
     * calls elements processContent() if it exist
     * here someone can modify the template
     *
     */
    public processContent(): void {
        this.logger.log('processContent');

        if (this._class.processContent) {
            this._process = this._class.processContent(this.template);
        }
    }



    /**
     * calls elements contentProcessed() if it exist
     * is called after template is processed and added to element
     *
     */
    public contentProcessed(controllers: IControllerArray): void {
        this.logger.log('contentProcessed');

        if (this._class.contentProcessed && this._process) {
            this._class.contentProcessed(controllers);
        }
    }



    /**
     * calls elements attached() if it exist
     * when attached to parent
     *
     */
    public attached(): void {
        this.logger.log('attached');

        if (this._class.attached) {
            this._class.attached();
        }
    }



    /**
     * calls elements detached() if it exist and clears it
     * when about to be detached
     *
     */
    public detached(): void {
        this.logger.log('detached');

        if (this._class.detached) {
            this._class.detached();
        }

        // remove meta binding
        BindingEngine.unSubscribeClassMetaBinding(this._class);

        this.parent = null;
        this.element = null;
        this._class.$element = null;
        this._class.$parent = null;
        this._class.$attributes = null;
        this._class = null;

    }

}
