import { ViewParser } from '../view/index';
import { EventAggregator } from './eventAggregator';
import { inject } from '../decorator/index';
import { IElement, INode } from '../interface/index';
import { Queue } from './queue';
import { DebounceBehavior, ThrottleBehavior, SignalBehavior, TriggerBehavior } from '../behavior/index';
// built in attributes/elements
import { MfRouter } from '../element/index';


import {
    IfAttribute,
    ValueAttribute,
    DelgateEventsAttribute,
    TriggerEventsAttribute,
    RepeatAttribute,
    MiscAttributes,
    CssAttribute,
    ModelAttribute
} from '../attribute';



/**
 * Main framework class, this is returned in the configure function
 *
 */
@inject(EventAggregator, Queue)
export class MF {

    private node: INode;
    private app: IElement;
    private eventAggregator: EventAggregator;
    public count = 0;
    public queue: Queue;


    constructor(eventAggregator: EventAggregator, queue: Queue) {
        this.eventAggregator = eventAggregator;
        this.queue = queue;

        // polyfill
        if (!(window as any).Reflect) {
            (window as any).Reflect = Object;
        }

        // register built in attributes/elements
        this.register(
            MfRouter,
            ValueAttribute,
            IfAttribute,
            DelgateEventsAttribute,
            RepeatAttribute,
            MiscAttributes,
            CssAttribute,
            ModelAttribute,
            TriggerEventsAttribute,
            SignalBehavior,
            ThrottleBehavior,
            DebounceBehavior,
            TriggerBehavior);
    }



    /**
     * sets node and app
     *
     */
    public setRootApp(app: IElement) {

        this.app = app;

        // return this instance
        return this;
    }



    /**
     * starts application
     *
     */
    public async start(node: INode) {

        this.node = node;

        // set framework to main node
        ViewParser.parseAndCreateElement(this.app, this.node, (this as any));

        // remove
        this.eventAggregator.publish('MF', 'MF started');

    }



    /**
     * register new custom attributes, custom elements
     *
     */
    public register(..._class: any[]) {

        // does not need to do anything really
        // by using them the decorator will kick in and they will be registered

    }

}
