import { traverseAST, getBehavior } from './traverseAst';
import { createBindingExpression, removeBindingExpression } from './createBindingExpression';
import { IListener } from '../interface/index';
import { Queue } from '../utils/index';
import { ContainerClasses, ContainerBehavior } from '../container/index';



/**
 * Register/creates a property observer with expression
 * It be called on changes to value of expression
 *
 */
export class PropertyObserverHandler {
    private expression: string;
    private ast: any;
    private context: Object;
    private observing: boolean;
    private listener: IListener;
    private value: any = undefined;
    private oldValue: any = undefined;
    private newValue: any = undefined;
    private isWaiting = false;
    private isNew = true;
    private queue: Queue;
    public curBehavior: any;


    constructor(expression: string, listener: IListener) {
        this.expression = expression;
        this.listener = listener;
        this.queue = ContainerClasses.get(Queue);
    }



    /**
     * binds observer handler to passed in context
     *
     */
    public bind(context: Object) {
        this.observing = true;
        this.context = context;
        createBindingExpression(this.expression, this.context, this);
    }


    /**
     * gets new AST "createNewBindingExpression"
     *
     */
    public setAst(ast: Object) {

        this.ast = ast;
        if (!this.curBehavior) {
            this.connectBehavior();
        }

    }



    /**
     * Sets init value
     *
     */
    public init() {
        if (this.isNew) {
            this.isNew = false;
            const oldValue = this.value;
            const newValue = traverseAST(this.ast, this.context);
            this.value = newValue;
            this.listenerCall(newValue, oldValue);
            this.isNew = false;
        }
    }



    /**
     * connectBehavior
     * only used for signal
     *
     */
    public connectBehavior() {
        const behaviors = getBehavior(this.ast);
        if (behaviors) {
            behaviors.forEach((behavior: { name: string, args: any[] }) => {
                if (name === 'signal') { // todo: open for more later ?
                    const x = ContainerBehavior.findBehavior(behavior.name);
                    if (x) {
                        this.curBehavior = new x(this, behavior.args);
                    }
                }
            });
        }
    }



    /**
     * Gets called by observer, it then calls the listener
     *
     */
    public update() {
        if (this.observing) {
            this.oldValue = this.value;
            this.newValue = traverseAST(this.ast, this.context);
            if (this.oldValue !== this.newValue) {
                this.value = this.newValue;
                if (!this.isWaiting) {
                    this.isWaiting = true;
                    // queue this, so we give browser time to breath
                    this.queue.add(this);
                }
            } else {
                this.oldValue = null;
                this.newValue = null;
            }
            // do not need to unbind, since all binding get thrown after its been called
            this.bind(this.context);
        }
    }

    public listenerCall(newValue: any, oldValue: any) {
        if (this.listener) {
            this.listener.call(newValue, oldValue);
        }
    }

    /**
     * Gets called by queue, then it calls subscriber
     *
     */
    public call(done: Function) {

        const newValue = this.newValue;
        const oldValue = this.oldValue;
        this.oldValue = null;
        this.newValue = null;
        this.listenerCall(newValue, oldValue);
        this.isWaiting = false;
        done();
    }

    /**
     * Unbinds and clears all internals
     *
     */
    public unbind() {

        if (this.observing) {
            removeBindingExpression(this.expression, this.context, this);
        }

        // remove this from caller
        this.listener.caller = null;

        // remove rest of internals
        // todo: Do I need to do this, Im I making grabage collection worse?
        this.observing = false;
        this.context = null;
        this.listener = null;
        this.value = null;
    }

}
