const element = new Map();


/**
 * keeps track of custom elements, so I know when parsing the html
 *
 */
export class ContainerElements {



    /**
     * registers new custom element
     *
     */
    public static regElement(_class: any, name: string) {
        if (!element.has(name)) {
            element.set(name, _class);
        }
    }



    /**
     * returns a class if found
     *
     */
    public static findElement(name: string): any | null {
        if (element.has(name)) {
            return element.get(name);
        } else {
            return null;
        }
    }



}
