import { customAttribute } from '../decorator/index';
import { IElement, IAttribute } from '../interface/index';
import { BindingEngine } from '../binding/index';



/**
 * css replaces style in elements
 *
 */
@customAttribute('css')
export class CssAttribute implements IAttribute {
    public $element: Element;
    public $attribute: Attr;
    public $parent: IElement;
    private subscribeInternal: any;
    private value: string;



    constructor() {

        // todo?

    }



    /**
     * created
     *
     */
    public created() {



        this.value = this.$attribute.value;
        this.$element.removeAttribute(this.$attribute.name);

        this.subscribeInternal = {
            name: 'cssAttribute:',
            value: this.value,
            call: (newValue: any, oldValue: any) => {
                if (oldValue !== newValue) {
                    this.splitAndInsert(newValue);
                }
            }
        };
        BindingEngine.subscribeClassProperty(this.$parent, this.value, this.subscribeInternal);
    }



    /**
     * splits new value into array/maps it and sets it to style
     * TODO: save state and remove old
     *
     */
    public splitAndInsert(value: string) {
        try {
            const x = value
                .split(';')
                .map((statement: any) => {
                    return statement.split(':');
                }).map((result: any) => {
                    return {
                        attribute: result[0],
                        value: result[1].replace(/ /g, '')
                    };
                });
            x.forEach((val) => {
                (<any>this.$element).style[val.attribute.trim()] = val.value.trim();
            });
        } catch (e) {
            console.error('could not parse css values');
        }
    }



    /**
     * detached, remove binding
     *
     */
    public detached() {
        BindingEngine.unSubscribeClassProperty(this.$parent, this.subscribeInternal);
        // todo
    }



    /**
     * attached
     *
     */
    public attached() {
        // nothing atm
    }



}
