import { ITokens } from '../interface/index';
import { AST } from './ast';
import { SymbolContainer } from './symbolContainer';
import { addSymbols } from './addSymbols';

const ast = new AST();
const symbolContainer = new SymbolContainer();
ast.addSymbolContainer(symbolContainer);
addSymbols(ast);

export const generateAST = function (tokenArray: ITokens[]) {

    return ast.start(tokenArray);

};
