// this is very simple atm
// need authStep, child router, on route not found fail back



export class Router {

    public config: any;
    public routes: any[];
    public hash: any;
    public mainRouter: any;
    public curRoute = 0;
    public started = true;
    public isNavigating = true;
    public title: string;
    public routersElements: any[] = [];

    constructor() {
        this.started = false;
        this.hash = window.location.hash;
        this.config = {
            root: '#/',
            title: null,
            map: (routes: any[]) => {
                this.routes = routes;
            },
            loader: null
        };
    }



    /**
     * triggers when hash changes
     *
     */
    public hashChange() {
        this.isNavigating = true;
        setTimeout(() => {

            this.hash = window.location.hash.slice(1).split('/');

            if (window.location.hash !== '#/' && window.location.hash !== '') {
                const hash = window.location.hash.slice(1).split('/');
                let success = false;

                this.routes.forEach((route, i) => {
                    if (hash[1] === route.route) {
                        this.curRoute = i;
                        success = true;
                    }
                });

                if (this.mainRouter && success) {
                    this.mainRouter.update();
                }

            }
            if (window.location.hash === '') {
                this.curRoute = 0;
                this.mainRouter.update();
            }

        }, 60);
    }



    /**
     * register router element (need fro child routers)
     *
     */
    public registerElement(el: any) {
        if (!this.mainRouter) {
            this.mainRouter = el;
        } else {
            this.routersElements.push(el);
        }

    }


    /**
     * unregister router element (need fro child routers)
     *
     */
    public unregisterElement(el: any) {
        if (this.mainRouter === el) {
            this.mainRouter = null;
        } else {
            if (this.routersElements.indexOf(el) !== -1) {
                this.routersElements.splice(this.routersElements.indexOf(el), 1);
            }
        }
    }



    /**
     * called when it starts
     *
     */
    public addConfig(config: any) {
        if (!this.started) {
            this.config = config;
            this.routes = config.routes;
            this.init();
        }

    }



    /**
     * called when it starts
     *
     */
    public init() {
        this.started = true;
        this.hash = this.config.root;
        this.title = this.config.title;
        window.addEventListener('hashchange', this.hashChange.bind(this));
        this.routes.forEach((route) => {
            // todo: need to have option for push state
            route.href = '#/' + route.route;
        });

        if (window.location.hash !== '#/' && window.location.hash !== '' && this.hash !== window.location.hash) {
            this.hash = window.location.hash.slice(1).split('/');
            let success = false;

            this.routes.forEach((route, i) => {
                if (this.hash[1] === route.route) {
                    this.curRoute = i;
                    success = true;
                }
            });

            if (this.mainRouter && success) {
                this.mainRouter.update();
            }

        }
    }



    /**
     * returns current module path from router config
     *
     */
    public getCurrentModule(): string {
        return this.routes[this.curRoute].modulePath;
    }



    /**
     * returns current module class(name) from router config
     *
     */
    public getCurrentClass(): string {
        return this.routes[this.curRoute].moduleClass;
    }



    /**
     * returns the content of router config load function (promise)
     *
     */
    public async loadCurrentModule(): Promise<string> {
        // this need to be async..
        return await this.routes[this.curRoute].load();
    }



}
