// cache maps
const templateMap = new Map();
const expressionMap = new Map();
const astMap = new Map();
const splitMap = new Map();


/**
 * Cache for storing ast, templates etc o speed up misc
 *
 */
export class Cache {

    public static clear() {
        templateMap.clear();
        expressionMap.clear();
        astMap.clear();
        splitMap.clear();
    }



    // template

    public static templateMapHas(x: any) {
        return templateMap.has(x);
    }

    public static templateMapSet(x: any, y: any) {
        return templateMap.set(x, y);
    }

    public static templateMapGet(x: any) {
        return templateMap.get(x);
    }



    // expressions

    public static expressionMapHas(x: any) {
        return expressionMap.has(x);
    }

    public static expressionMapSet(x: any, y: any) {
        return expressionMap.set(x, y);
    }

    public static expressionMapGet(x: any) {
        return expressionMap.get(x);
    }



    // ast

    public static astMapHas(x: any) {
        return astMap.has(x);
    }

    public static astMapSet(x: any, y: any) {
        return astMap.set(x, y);
    }

    public static astMapGet(x: any) {
        return astMap.get(x);
    }



    // string splits

    public static splitMapHas(x: any) {
        return splitMap.has(x);
    }

    public static splitMapSet(x: any, y: any) {
        return splitMap.set(x, y);
    }

    public static splitMapGet(x: any) {
        return splitMap.get(x);
    }

}
