
import { behavior } from '../decorator/index';

@behavior('updateTrigger')
export class TriggerBehavior {
    public name: string;
    constructor(binding: any, args: any) {
        const trigger = args;
        if (binding.eventHandlerBinded) {

            if ('trigger' in binding && trigger) {
                binding.trigger = trigger;
            }

        }
    }
}
