import { ClassArrayObserverCreator } from './classArrayObserverCreator';
import { IListener } from '../interface/index';
import { Queue } from '../utils/index';
import { ContainerClasses } from '../container/index';


/**
 * Register/creates a array observer with expression
 * It be called on changes and then calls listener
 *
 */
export class ArrayObserverHandler {
    private expression: string;
    private context: any;
    private observing: boolean;
    private listener: IListener;
    private isWaiting = false;
    private data: any = undefined;
    private queue: Queue;


    constructor(expression: string, listener: IListener) {
        this.expression = expression;
        this.listener = listener;
        this.isWaiting = null;
        this.queue = ContainerClasses.get(Queue);
    }



    /**
     * binds observer handler to passed in context
     *
     */
    public bind(context: Object) {
        this.observing = true;
        this.context = context;
        ClassArrayObserverCreator.create(this.context, this.expression, this);
    }



    /**
     * Gets called by observer, it then calls the listener
     *
     */
    public update(data: any) {
        this.data = data;
        if (this.observing) {
            if (!this.isWaiting) {
                this.isWaiting = true;
                // queue this, so we give browser time to breath
                this.queue.add(this);
             }
            // always rebind, since context might have change/array have been replaced
            this.bind(this.context);
        }
    }



    /**
     * Gets called by queue, then it calls subscriber
     *
     */
    public call(done: Function) {
          const data = this.data;
        this.data = null;
        if (this.listener) {
            this.listener.call(data);
        }
        this.isWaiting = false;
        done();
    }



    /**
     * Unbinds and clears all internals
     *
     */
    public unbind() {

        // if observing remove observer
        if (this.observing) {
            ClassArrayObserverCreator.remove(this.context, this.expression, this);
        }

        // remove this from caller
        this.listener.caller = null;

        // remove rest of internals
        // todo: Do I need to do this, Im I making grabage collection worse?
        this.listener = null;
        this.observing = false;
        this.context = null;
        this.data = null;
        this.expression = null;
    }

}
