import { customAttribute } from '../decorator/index';
import { IAttribute, IElement } from '../interface/index';
import { BindingEngine } from '../binding/index';
/*import { IListener } from '../interface/i_Listener';
import { ContainerValueConverters, ContainerClasses } from '../container';
 */



@customAttribute('model.bind')
export class ModelAttribute implements IAttribute {

    // build in properties added by our template engine
    public $element: HTMLInputElement;
    public $attribute: Attr;
    public $parent: IElement;
    public value: any;

    private subscribeInternal: any;
    private expression: any;


    /**
     * created
     *
     */
    public created() {

        (this.$attribute as any).getContent = () => {
            return this;
        };
        this.expression = this.$attribute.value;

        this.subscribeInternal = {
            name: 'modelAttribute:',
            value: this.value,
            call: (newValue: any, oldValue: any) => {
                if (oldValue !== newValue) {
                    this.value = newValue;
                }
            }
        };
        BindingEngine.subscribeClassProperty(this.$parent, this.expression, this.subscribeInternal);

    }


    /**
     * detached
     *
     */
    public detached() {

        (this.$attribute as any).getContent = null;
    }

}
