import { traverseAST } from './traverseAst';
import { tokenize } from './tokenize';
import { generateAST } from './generateAst';
import { Cache } from '../utils/index';

/**
* creates binding expression by getting tokens from the text
*
*/
export function evaluateExpression(text: string, _class: any) {
    if (text) {
        let ast: any;
        if (Cache.astMapHas(text)) {
            ast = Cache.astMapGet(text);
        } else {
            const tokens = tokenize(text);
            ast = generateAST(tokens);
            Cache.astMapSet(text, ast);
        }

        return traverseAST(ast, _class);
    } else {
        return '';
    }
}
