import { Router } from '../utils/router';
import { ViewController } from '../view/viewController';
import { IControllerArray } from '../interface';

export interface IElement {

    // DOM Element to the custom element
    $element?: keyof ElementTagNameMap | any;


    // DOM attributes on Element
    $attributes?: NamedNodeMap;


    // parent class
    $parent?: IElement;


    // view controller, will have more helper functions later.
    $view?: ViewController;


    // mandatory( one of them), need to return template
    // all helper variables are set here
    loadTemplate?(): string;
    loadTemplateAsync?(): Promise<string>;

    // when created, and $element etc is set
    created?(templateNode?: Node): void;


    // middleware, incase you want to mess with it
    // important to return true/false
    processContent?(templateNode?: Node): boolean;


    // middleware, get controllers of content processed
    // content are added to element, but attached is not called on any of them
    contentProcessed?(controllers?: IControllerArray): void;


    // when element node is attached to parent
    // you want to add event listeners here
    attached?(): void;


    // when element node is about to be detached from parent
    detached?(): void;



    // only when element used as page in router
    configureRouter?(config: any, router: Router): void;

    // only when element used as page in router
    // todo: should we return promise here ?
    canDeactivate?(): boolean;

    // only when element used as page in router
    activated?(): void;
}
