import { IElement } from './i_Element';

export interface IAttribute {

  // DOM Element to the custom attribute
  $element?: keyof ElementTagNameMap | any;


  // DOM attributes on Element
  $attribute?: Attr;


  // parent class
  $parent?: IElement;

  // when created, and $element etc is set
  created?(): void;


  // when its element is attached to parent node
  attached?(): void;


  // when its element is detached from parent node
  detached?(): void;

  // typical event handler for behavior
  eventHandlerBinded?(): void;
}


