import { ContainerValueConverters, ContainerClasses } from '../container/index';

let fromView: any;
const splitCache = {};
const splitLocalCache = {};

const Traverser = class {



    /**
     * operator calls
     *
     */
    public static operator = function (val: string, a: any, b: any) {
        switch (val) {
            case '+': return a + b;
            case '-': return b ? a - b : -a;
            case '*': return a * b;
            case '!': return !a;
            case '%': return a % b;
            case '>': return a > b;
            case '<': return a < b;
            case '>=': return a >= b;
            case '&&': return a && b;
            case '||': return a || b;
            case '<=': return a <= b;
            case '===': return a === b;
            case '!==': return a !== b;
            case '/': return a / b;
            // tslint:disable-next-line:no-bitwise
            case '^': return a ^ b;
        }
    };



    /**
     * finds out what arity it is
     *
     */
    public static checkArity = function (ast: any, ctx: any) {
        let value;
        if (ast && ast.arity === 'literal') {
            value = ast.value;
        }

        if (ast && ast.arity === 'variable') {
            value = Traverser.variable(ast, ctx);
        }

        if (ast && ast.arity === 'binary') {
            value = Traverser.binary(ast, ctx);
        }

        if (ast && ast.arity === 'unary') {
            value = Traverser.binary(ast, ctx);
        }

        if (ast && ast.arity === 'ternary') {
            value = Traverser.ternary(ast, ctx);
        }

        if (ast && ast.arity === 'valueConverter') {
            value = Traverser.valueConverter(ast, ctx);
        }

        return value;
    };



    /**
     * called if arity is variable
     * todo: this one is kinda messy atm
     *
     */
    public static variable = function (ast: any, ctx: any) {
        let result;
        try {


            switch (true) {
                case ast.value === 'null' && ast.arity === 'variable':
                    return null;

                case ast.value === 'undefined' && ast.arity === 'variable':
                    return undefined;

                case ast.arity === 'variable' && typeof ast.value === 'string' && ast.value.toUpperCase() === 'TRUE':
                    return true;

                case ast.arity === 'variable' && typeof ast.value === 'string' && ast.value.toUpperCase() === 'FALSE':
                    return false;

                default:
            }

            let keys;
            let keyCtx;
            // split text
            if (!splitCache[ast.value]) {
                keys = ast.value.split(/\.|\[([^\]]+)\]\.?/).filter((x: any) => x ? x : null);
                splitCache[ast.value] = keys;
                splitLocalCache[ast.value] = typeof keys[0] === 'string' && keys[0][0] !== '$';
            } else {
                keys = splitCache[ast.value];
                keyCtx = splitLocalCache[ast.value];
            }

            // more then 1, then its array
            let curObj: any;
            let curCtx = ctx;
            if (ctx.isMultiContext) {
                // multi context will be in events
                curCtx = ctx.ctx;
            }

            for (let i = 0; i < keys.length; i++) {
                if (keys[i]) {
                    if (i === 0) {
                        curObj = curCtx[keys[i]];
                        if (keyCtx) {
                            // check for override context
                            if (curCtx && curCtx.overrideContext) {
                                while (curCtx.overrideContext) {
                                    if (keys[i] in curCtx.overrideContext) {
                                        curObj = curCtx.overrideContext[keys[i]];
                                    }
                                    curCtx = curCtx.overrideContext;
                                }
                            }
                        }

                    } else {
                        curObj = curObj[keys[i]];
                    }
                }
            }

            if (ctx.isMultiContext && curObj === undefined) {
                curCtx = ctx;
                keys.forEach((prop: any, i: any) => {
                    if (prop) {
                        if (i === 0) {
                            curObj = curCtx[prop];
                            if (typeof prop === 'string' && prop[0] !== '$') {
                                if (curCtx && curCtx.overrideContext) {
                                    while (curCtx.overrideContext) {
                                        if (prop in curCtx.overrideContext) {
                                            curObj = curCtx.overrideContext[prop];
                                        }
                                        curCtx = curCtx.overrideContext;
                                    }
                                }
                            }
                        } else {
                            curObj = curObj[prop];
                        }
                    }
                });
            }

            result = curObj;
        } catch (e) {
            // we dont want to display this just nothing if there is no var
            // console.log('failed in variable');
            // nothing
        }

        return result;
    };



    /**
     * called if arity is binary
     *
     */
    public static binary = function (ast: any, ctx: any) {
        let second: any;

        if (!ast.isFunction) {

            /************************************* */
            const first = Traverser.checkArity(ast.first, ctx);
            second = Traverser.checkArity(ast.second, ctx);

            /************************************* */

            const value = Traverser.operator(ast.value, first, second);

            return value;

        } else {
            const results: any[] = [];

            ast.second.forEach(function (res: any) {
                results.push(Traverser.evaluate(res, ctx));
            });

            let curCtx = ctx;

            // a bit messy this part...
            if (ctx.isMultiContext) {
                curCtx = ctx.ctx;
            }

            let valueTrigger: any = curCtx[ast.first.value];

            if (curCtx && curCtx.overrideContext) {
                while (curCtx.overrideContext) {

                    if (curCtx.overrideContext[ast.first.value]) {
                        valueTrigger = curCtx.overrideContext[ast.first.value];
                    }

                    curCtx = curCtx.overrideContext;
                }
            }

            const value = valueTrigger.apply(curCtx, results);

            return value;
        }
    };



    /**
     * ternary
     * called if arity is ternary (conditional)
     * {condition ? expr1 : expr2}'
     */
    public static ternary = function (ast: any, ctx: any) {

        const first = Traverser.checkArity(ast.first, ctx);
        const second = Traverser.checkArity(ast.second, ctx);
        const third = Traverser.checkArity(ast.third, ctx);

        const value: any = first ? second : third;

        return value;
    };



    /**
     * called if arity is value converter
     *
     */
    public static valueConverter = function (ast: any, ctx: any) {
        let result;

        const args: any[] = [];
        // loop args
        ast.args.forEach((arg: any) => {
            args.push(Traverser.checkArity(arg, ctx));
        });

        // find value converter
        const valueConverterExist = ContainerValueConverters.findConverter(ast.value);
        let _class: any;

        // if it exist, then we execute it
        if (valueConverterExist) {
            _class = ContainerClasses.get(valueConverterExist);
            try {
                result = fromView ? _class.fromView.apply(ctx, args) : _class.toView.apply(ctx, args);
            } catch (e) {
                console.log('value converter failed:' + ast.value);
            }
        } else {
            console.log('value converter does not exist:' + ast.value);
        }

        return result;
    };



    /**
     * called to start evaluating ast (getting result)
     *
     */
    public static evaluate = function (astInput: any, ctx: any) {

        fromView = false;

        // if more then its
        const astArray = Array.isArray(astInput) ? astInput : [astInput];
        const returnArray: any[] = [];
        // astArray.forEach((ast: any) => {
        for (let i = 0; i < astArray.length; i++) {


            let result;
            switch (true) {
                case astArray[i].arity === 'literal':
                    result = astArray[i].value;
                    break;
                case astArray[i].arity === 'unary':
                    result = Traverser.binary(astArray[i], ctx);
                    break;
                case astArray[i].arity === 'binary':
                    result = Traverser.binary(astArray[i], ctx);
                    break;
                case astArray[i].arity === 'variable':
                    result = Traverser.variable(astArray[i], ctx);
                    break;
                case astArray[i].arity === 'ternary':
                    result = Traverser.ternary(astArray[i], ctx);
                    break;
                case astArray[i].arity === 'behavior':
                    // this will be used outside this scope for now
                    break;
                case astArray[i].arity === 'valueConverter':
                    // resolve args
                    result = Traverser.valueConverter(astArray[i], ctx);
                    break;
                default:
                    console.log('unknown expression');
            }
            returnArray.push(result);

        }

        // });
        if (returnArray.length > 1) {
            return returnArray.join('');
        } else {
            return returnArray[0];
        }

    };



    /**
     * handle value converter fromView only
     *
     */
    public static traverseOnlyValueConverter = function (value: any, ast: any, ctx: any) {
        let result;

        let args: any[] = [];
        // loop args
        ast.args.forEach((arg: any, i: number) => {
            if (i > 0) {
                args.push(Traverser.checkArity(arg, ctx));
            }

        });

        args = [value, ...args];

        // find value converter
        const valueConverterExist = ContainerValueConverters.findConverter(ast.value);
        let _class: any;

        // if it exist, then we execute it
        if (valueConverterExist) {
            _class = ContainerClasses.get(valueConverterExist);
            try {
                result = fromView ? _class.fromView.apply(ctx, args) : _class.toView.apply(ctx, args);
            } catch (e) {
                console.log('value converter failed:' + ast.value);
            }
        } else {
            console.log('value converter does not exist:' + ast.value);
        }

        return result;
    };



    /**
     * evaluate ast
     *
     */
    public static evaluateConverterFromViewOnly = function (astInput: any, value: any, ctx: any) {

        // if more then its
        const astArray = Array.isArray(astInput) ? astInput : [astInput];
        const returnArray: any[] = [];
        fromView = true;
        let valueConverted = false;
        // astArray.forEach((ast: any) => {
        for (let i = 0; i < astArray.length; i++) {


            let result;
            switch (true) {
                case astArray[i].arity === 'valueConverter':
                    // resolve args
                    valueConverted = true;
                    result = Traverser.traverseOnlyValueConverter(value, astArray[i], ctx);
                    break;
            }
            if (result) {
                returnArray.push(result);
            }


        }

        // });
        if (returnArray.length > 1) {
            return returnArray.join('');
        } else {
            if (valueConverted) {
                return returnArray[0];
            } else {
                return value;
            }
        }

    };



    /**
     * gets the behaviors from ast
     *
     */
    public static getBehaviorFunctions = function (ast: any) {
        const astArray = Array.isArray(ast) ? ast : [ast];
        const returnArray: any[] = [];
        // astArray.forEach((ast: any) => {
        for (let i = 0; i < astArray.length; i++) {


            let result;
            switch (true) {
                case astArray[i].arity === 'behavior':
                    result = {
                        name: astArray[i].value,
                        args: astArray[i].args.map((a: any) => a.value)
                    };
                    // this will be used outside this scope for now
                    break;
            }
            if (result) {
                returnArray.push(result);
            }


        }

        // });
        return returnArray;

    };

};


// default for getting result
export const traverseAST = function (ast: any, ctx: any) {
    return Traverser.evaluate(ast, ctx);
};

// used for getting valueConverter
export const valueConvert = function (ast: any, ctx: any, value: any) {
    return Traverser.evaluateConverterFromViewOnly(ast, value, ctx);
};

// return behavior
export const getBehavior = function (ast: any) {
    return Traverser.getBehaviorFunctions(ast);
};
