import { ContainerElements } from '../container/index';
import { ContainerAttributes } from '../container/index';
import { Cache } from '../utils/index';
import { AttributeController } from './attributeController';
import { ElementController } from './elementController';
import { InterpolateController } from './interpolateController';
import { ViewController } from './viewController';
import { IElement, IRepeatCache, IControllerArray, INode } from '../interface/index';



/**
 * This class have methods to parse the string/template and initiate our custom elements/attributes/binding
 *
 */
export class ViewParser {

    /**
     * creates element and parses template string
     * used when starting mframejs and router
     *
     */
    public static parseAndCreateElement(_class: IElement, element: INode, parent: IElement, templateString?: string): void {
        const elementController = new ElementController(parent, element, _class, null, templateString);
        const controllers = elementController.init();
        elementController.attached();
        controllers.forEach((controller) => {
            if (controller.attached) {
                controller.attached();
            }
        });
    }



    /**
     * cleans a html template
     *
     */
    public static cleanTemplate(template: any): void {
        // loop children

        const loopNodes = (node: INode) => {
            for (let n = 0; n < node.childNodes.length; n++) {

                // get child
                let child = node.childNodes[n];

                if (child.nodeType === 8 || (child.nodeType === 3 && !/\S/.test(child.nodeValue))) {

                    // 8 Represents a comment
                    // Represents textual content in an element or attribute, but check if empty
                    node.removeChild(child);
                    n--;

                } else if (child.nodeType === 1) {

                    // 1 = Element, Text, Comment, ProcessingInstruction, CDATASection, EntityReference
                    loopNodes(child);
                }
                child = null;
            }
        };
        loopNodes(template);


    }



    /**
     * creates a template from string markup
     *
     */
    public static createTemplate(tag: string, markup: string): Element {
        let template: any;

        // see if we have template cached
        if (!Cache.templateMapHas(markup)) {

            // create empty div
            const container = document.createElement('div');

            // add markup
            container.innerHTML = (<any>markup).default || markup;

            // get first element (template)
            // todo, check if it contains template tag!
            const fragment = container.firstElementChild;
            if (!(<any>fragment).content) {
                // ie11 fix
                (<any>fragment).content = document.createDocumentFragment();
                while (fragment.childNodes[0]) {
                    (<any>fragment).content.appendChild(fragment.childNodes[0]);
                }
            }

            // create element tag or a dummy
            template = document.createElement(tag || 'mf-template');

            // clean up all empty text nodes
            ViewParser.cleanTemplate((fragment as any).content);

            // add template
            template.appendChild((fragment as any).content);

            Cache.templateMapSet(markup, template);

        } else {
            template = Cache.templateMapGet(markup);
        }

        // return clone
        const x = template.cloneNode(true);
        template = null;

        return x;
    }



    /**
     * Parses element and its children for views and calls remove on them
     *
     */
    public static clearAllViews(element: INode): void {

        const nodeFiler = function (node: any): number {
            return node.$view ? NodeFilter.FILTER_ACCEPT : NodeFilter.FILTER_REJECT;
        };

        // https://developer.mozilla.org/en-US/docs/Web/API/NodeIterator
        const nodeIterator = document.createNodeIterator(
            element,
            NodeFilter.SHOW_ALL,
            (nodeFiler as any),
            false
        );
        let currentNode;
        while (currentNode = nodeIterator.nextNode()) {
            (currentNode as any).$view.clearView();
        }
    }



    /**
     * parses nodes and adds initiates elements/interpolate and attributes
     * use cache version when generating many
     */
    public static parseNodes(template: INode, parentContext: IElement, $view: ViewController): IControllerArray {

        const arr: IControllerArray = [];

        // loops and pushes into our node map/array
        const loopNodes = function (_node: INode) {

            // loop children
            for (let n = 0; n < _node.childNodes.length; n++) {

                // vars
                let anyControllerAttributes = false;
                // let anyRepeat = false;

                // get child
                const htmlNode = _node.childNodes[n];

                // first of all we need to check for master attributes like if.bind and repeat
                if ((htmlNode as any).getAttribute) {

                    const attributeNode = (htmlNode as any).getAttributeNode('if.bind');
                    if (attributeNode) {

                        // we have if.bind, we not need to create it
                        anyControllerAttributes = true;
                        const customAttribute = ContainerAttributes.findAttribute(attributeNode.name);

                        const instance = new AttributeController(parentContext, htmlNode, attributeNode, customAttribute, $view);
                        $view = (htmlNode as any).$view ? (htmlNode as any).$view : $view;
                        instance.init();

                        if (instance) {
                            arr.push(instance);
                        }

                    } else {

                        // no if.bind,lets check for repeat.for

                        const attributeNode = (htmlNode as any).getAttributeNode('repeat.for');
                        if (attributeNode) {

                            anyControllerAttributes = true;
                            // we have if bind, we not need to create it
                            const customAttribute = ContainerAttributes.findAttribute(attributeNode.name);
                            const instance = new AttributeController(parentContext, htmlNode, attributeNode, customAttribute, $view);
                            $view = (htmlNode as any).$view ? (htmlNode as any).$view : $view;
                            instance.init();
                            if (instance) {
                                arr.push(instance);
                            }
                        }

                    }
                }



                // if not controller attribute loop attributes and initiate them
                if (!anyControllerAttributes) {
                    const childAttributes = htmlNode.attributes || (<any[]>[]);
                    for (let i = 0; i < childAttributes.length; i++) {

                        const attr = childAttributes[i];

                        let isCustomAttribute = ContainerAttributes.findAttribute(attr.name);

                        if (!isCustomAttribute && attr.name) {
                            isCustomAttribute = ContainerAttributes.findAttribute(attr.name.replace('.bind', ''));

                            if (!isCustomAttribute && attr.name) {
                                isCustomAttribute = ContainerAttributes.findAttribute('mframejs-event-trigger.' + attr.name.split('.')[1]);

                                if (!isCustomAttribute && attr.name) {
                                    isCustomAttribute = ContainerAttributes.findAttribute('mframejs-event-delegate.' + attr.name.split('.')[1]);
                                }
                            }
                        }

                        if (isCustomAttribute) {
                            const instance = new AttributeController(parentContext, htmlNode, attr, isCustomAttribute, $view);
                            $view = (htmlNode as any).$view ? (htmlNode as any).$view : $view;
                            instance.init();
                            if (instance) {
                                arr.push(instance);
                            }
                        } else {
                            if (attr.value.indexOf('${') !== -1) {
                                const interpolateController = new InterpolateController(parentContext, attr, $view);
                                interpolateController.init();
                                arr.push(interpolateController);
                            }
                        }
                    }
                }



                // if not controller attributes then add custom element if there is any
                if (!anyControllerAttributes) {

                    // get ${} where its used and create binding expression
                    if ((<any>htmlNode).textContent && (<any>htmlNode).nodeType !== 1) {
                        if ((<any>htmlNode).textContent.indexOf('${') !== -1) {
                            const interpolateController = new InterpolateController(parentContext, htmlNode, $view);
                            interpolateController.init();
                            arr.push(interpolateController);
                        }
                    }

                    // node 1 = normal elements..
                    if (htmlNode.nodeType === 1) {
                        // check if its a custom element we found before...
                        const haveCustomElement = ContainerElements.findElement(htmlNode.localName);
                        if (haveCustomElement) {
                            // add element
                            const elementController = new ElementController(parentContext, htmlNode, null, haveCustomElement, null);
                            $view = (htmlNode as any).$view ? (htmlNode as any).$view : $view;
                            const controllers = elementController.init();
                            arr.push(elementController);
                            arr.push(...controllers);
                        } else {
                            // more children?
                            if (htmlNode.childNodes) {
                                loopNodes(htmlNode);
                            }
                        }
                    }

                }
            }
        };


        const tempTemplate = ({ childNodes: [template] } as any);
        loopNodes(tempTemplate);


        return arr;

    }



    /**
     * parses nodes and adds initiates elements/interpolate and attributes by using cache to speed up
     *
     */
    public static parseNodesWithCache(template: INode, parentContext: IElement, cache: Array<IRepeatCache>, $view: ViewController): IControllerArray {

        const arr: IControllerArray = [];

        const nodeIterator = document.createTreeWalker(
            template,
            NodeFilter.SHOW_ALL,
            null,
            false
        );

        let yy = 0;
        const last = cache[0].last;

        let htmlNode;
        for (let i = 0; i < cache.length; i++) {
            htmlNode = nodeIterator.nextNode();

            if (cache[i].no === yy) {
                switch (cache[i].type) {
                    case 'attribute-interpolate':
                    case 'attribute':
                        if (cache[i].type === 'attribute') {
                            const attributeController =
                                new AttributeController(parentContext, htmlNode, htmlNode.attributes[cache[i].attributeCount], cache[i].set, $view);
                            $view = (htmlNode as any).$view ? (htmlNode as any).$view : $view;
                            attributeController.init();
                            arr.push(attributeController);
                        } else {
                            const interpolateController = new InterpolateController(parentContext, htmlNode.attributes[cache[i].attributeCount], $view);
                            interpolateController.init();
                            arr.push(interpolateController);
                        }

                        while (cache[i + 1] && cache[i + 1].no === yy) {
                            i++;
                            if (cache[i].type === 'attribute') {
                                const attributeController =
                                    new AttributeController(parentContext, htmlNode, htmlNode.attributes[cache[i].attributeCount], cache[i].set, $view);
                                $view = (htmlNode as any).$view ? (htmlNode as any).$view : $view;
                                attributeController.init();
                                arr.push(attributeController);
                            } else {
                                const interpolateController = new InterpolateController(parentContext, htmlNode.attributes[cache[i].attributeCount], $view);
                                interpolateController.init();
                                arr.push(interpolateController);
                            }

                        }
                        break;
                    case 'element':
                        const elementController = new ElementController(parentContext, htmlNode, null, cache[i].set, null);
                        $view = (htmlNode as any).$view ? (htmlNode as any).$view : $view;
                        const controllers = elementController.init();
                        arr.push(elementController);
                        arr.push(...controllers);
                        break;
                    case 'interpolate':
                        const interpolateController = new InterpolateController(parentContext, htmlNode, $view);
                        interpolateController.init();
                        arr.push(interpolateController);
                        break;
                }
            }
            yy++;
            if (yy > last) {
                break;
            }

        }

        return arr;

    }



    /**
     * parses nodes and creates cache to be used with parser
     *
     */
    public static createNodeParseCache(template: INode): Array<IRepeatCache> {

        const cache: Array<IRepeatCache> = [];

        let attributeCount = -1;
        let yy = -1;

        let last = 0;

        const loopNodes = function (_node: INode, controllerOverride: any) {


            for (let n = 0; n < _node.childNodes.length; n++) {

                yy++;
                const cacheLength = cache.length;
                // vars
                let anyControllerAttributes = controllerOverride ? controllerOverride : false;

                const htmlNode = _node.childNodes[n];


                // first of all we need to check for master attributes like if.bind and repeat
                if ((htmlNode as any).getAttribute && !controllerOverride) {

                    const haveIfBindAttribute = (htmlNode as any).getAttribute('if.bind');
                    if (haveIfBindAttribute) {

                        anyControllerAttributes = true;
                        const childAttributes = htmlNode.attributes || (<any[]>[]);
                        attributeCount = -1;
                        for (let i = 0; i < childAttributes.length; i++) {
                            attributeCount++;
                            const attr = childAttributes[i];
                            if (attr.name === 'if.bind') {
                                const isCustomAttribute = ContainerAttributes.findAttribute(attr.name);
                                if (isCustomAttribute) {
                                    last = yy;
                                    cache.push({
                                        no: yy,
                                        type: 'attribute',
                                        attributeCount: attributeCount,
                                        set: isCustomAttribute
                                    });
                                }
                            }
                        }

                    } else {

                        const haveRepeatAttribute = (htmlNode as any).getAttribute('repeat.for');
                        if (haveRepeatAttribute) {

                            anyControllerAttributes = true;
                            const childAttributes = htmlNode.attributes || (<any[]>[]);
                            attributeCount = -1;
                            for (let i = 0; i < childAttributes.length; i++) {
                                attributeCount++;
                                const attr = childAttributes[i];
                                if (attr.name === 'repeat.for') {
                                    const isCustomAttribute = ContainerAttributes.findAttribute(attr.name);
                                    if (isCustomAttribute) {
                                        last = yy;
                                        cache.push({
                                            no: yy,
                                            type: 'attribute',
                                            attributeCount: attributeCount,
                                            set: isCustomAttribute
                                        });
                                    }
                                }
                            }
                        }
                    }
                }



                // if not controller attribute loop attributes and initiate them
                if (!anyControllerAttributes && !controllerOverride) {
                    const childAttributes = htmlNode.attributes || (<any[]>[]);
                    attributeCount = -1;
                    for (let i = 0; i < childAttributes.length; i++) {
                        attributeCount++;

                        const attr = childAttributes[i];
                        // get instance (always new)
                        let isCustomAttribute = ContainerAttributes.findAttribute(attr.name);
                        if (!isCustomAttribute && attr.name) {
                            isCustomAttribute = ContainerAttributes.findAttribute(attr.name.replace('.bind', ''));

                            if (!isCustomAttribute && attr.name) {
                                isCustomAttribute = ContainerAttributes.findAttribute('mframejs-event-trigger.' + attr.name.split('.')[1]);

                                if (!isCustomAttribute && attr.name) {
                                    isCustomAttribute = ContainerAttributes.findAttribute('mframejs-event-delegate.' + attr.name.split('.')[1]);
                                }
                            }
                        }

                        if (isCustomAttribute) {
                            last = yy;
                            cache.push({
                                no: yy,
                                type: 'attribute',
                                attributeCount: attributeCount,
                                set: isCustomAttribute
                            });
                        } else {
                            if (attr.value.indexOf('${') !== -1) {
                                last = yy;
                                cache.push({
                                    no: yy,
                                    type: 'attribute-interpolate',
                                    set: null,
                                    attributeCount: attributeCount
                                });
                            }
                        }
                    }
                }



                // if not controller attributes then add custom element if there is any
                if (!anyControllerAttributes && !controllerOverride) {

                    if ((<any>htmlNode).textContent && (<any>htmlNode).nodeType !== 1) {
                        if ((<any>htmlNode).textContent.indexOf('${') !== -1) {
                            last = yy;
                            cache.push({
                                no: yy,
                                type: 'interpolate',
                                set: null,
                                attributeCount: null
                            });
                        }
                    } else {
                        // node 1 = normal elements..
                        if (htmlNode.nodeType === 1) {
                            // check if its a custom element we found before...
                            const haveCustomElement = ContainerElements.findElement(htmlNode.localName);
                            if (haveCustomElement) {
                                last = yy;
                                cache.push({
                                    no: yy,
                                    type: 'element',
                                    set: haveCustomElement,
                                    attributeCount: null
                                });
                            }
                        }
                    }
                }

                if (cacheLength === cache.length) {
                    cache.push({
                        no: yy + 99,
                        type: null,
                        set: null,
                        attributeCount: null
                    });
                }

                if (htmlNode.childNodes) {
                    loopNodes(htmlNode, anyControllerAttributes);
                }
            }
        };

        // let tempTemplate = ({ childNodes: [template] } as any);
        loopNodes(template, false);
        cache[0].last = last;

        return cache;

    }
}
