import { customAttribute } from '../decorator/index';
import { IElement, IAttribute } from '../interface/index';
import { BindingEngine } from '../binding/index';
import { ContainerBehavior } from '../container/index';
import { Cache } from '../utils/index';

/**
 * uses what ever name it gets with ".delegate" and uses the name as event listener
 *
 */
@customAttribute('mframejs-event-delegate.delegate')
export class DelgateEventsAttribute implements IAttribute {

    public $element: HTMLInputElement;
    public $attribute: Attr;
    public $parent: IElement;
    private value: string;
    private name: string;
    private expressionValue: string;
    // private eventID: number;
    public eventHandlerBinded: any;



    constructor() {
        // na
    }



    /**
     * created
     *
     */
    public created() {

        this.name = (this.$attribute as any).name.replace('.delegate', '');
        this.value = (this.$attribute as any).value;
        this.expressionValue = (this.$attribute as any).value;
        this.eventHandlerBinded = this.eventHandler.bind(this);
        this.connectBehavior();

        // todo... this isn't really delegate atm, just trigger really!!

        (this.$element as any).addEventListener(this.name, (this.eventHandlerBinded as EventListenerOrEventListenerObject), false);
    }



    /**
     * attached
     *
     */
    public attached() {
        // nothing atm
    }



    /**
     * detached
     *
     */
    public detached() {
        // this.domEventHandler.removeListener(this.eventID);
        // <- I was thinking all wrong here, need to fix, attach element for now


        this.$element.removeEventListener(this.name, this.eventHandlerBinded);
    }



    /**
     * when event triggers it uses the value with ast
     *
     */
    public eventHandler(event: any): void {
        BindingEngine.tokenizeParseAndTraverseAST(this.value, {
            ctx: this.$parent,
            $event: event,
            isMultiContext: true
        });
    }



    /**
     * connectBehavior
     * todo: move into utils
     *
     */
    public connectBehavior() {
        if (this.expressionValue.indexOf('&') !== -1) {
            let x = Cache.expressionMapGet(this.expressionValue);
            if (!x) {
                const tokens: any = BindingEngine.tokenize(this.expressionValue);
                x = {};
                x.ast = BindingEngine.generateAST(tokens);
            }
            const behaviors = BindingEngine.getBehavior((<any>x).ast);
            if (behaviors) {
                behaviors.forEach((behavior: { name: string, args: any[] }) => {
                    const x = ContainerBehavior.findBehavior(behavior.name);
                    if (x) {
                        let curBehavior: any = new x(this, behavior.args);
                        curBehavior = curBehavior;
                    }
                });
            }
        }
    }

}
