import { ViewController } from './viewController';
import { ContainerClasses } from '../container/index';
import { BindingEngine } from '../binding';
import { IAttribute, IElement, INode } from '../interface/index';
import { Logger } from '../utils/index';

/**
 * start element life cycle
 *
 */
export class AttributeController {
    private class: IAttribute;
    private logger: Logger;



    constructor(private parent: IElement, private element: INode, private attribute: Attr, attributeRef: any, $view: ViewController) {
        this.logger = Logger.getLogger(attribute.name, 'attribute');
        this.class = ContainerClasses.get(attributeRef);
        this.element = element;
        if ((attribute.name === 'if.bind' || attribute.name === 'repeat.for') && parent.$element) {
            // these are controllers so they need to create new view
            if (element && !element.$view) {
                element.$view = new ViewController(element);
                element.$view.addAttribute(this);
            }
        } else {
            $view.addAttribute(this);
        }

    }


    /**
     * start element life cycle
     *
     */
    public init(): void {
        this.create();
    }



    /**
     * sets vars and calls create on attribute
     *
     */
    public create(): void {
        this.logger.log('created', this.attribute.name, this.attribute.value);
        this.class.$element = this.element;
        this.class.$parent = this.parent;
        this.class.$attribute = this.attribute;

        // subscribe meta binding
        BindingEngine.subscribeClassMetaBinding(this.class);

        if (this.class.created) {
            this.class.created();
        }


    }



    /**
     * calls elements attached() if it exist
     *
     */
    public attached(): void {
        this.logger.log('attached', this.attribute.name, this.attribute.value);
        if (this.class.attached) {
            this.class.attached();
        }
    }



    /**
     *  calls elements detached() if it exist and clears it
     *
     */
    public detached(): void {
        this.logger.log('detached', this.attribute.name, this.attribute.value);

        if (this.class.detached) {
            this.class.detached();
        }

        // remove meta binding
        BindingEngine.unSubscribeClassMetaBinding(this.class);

        this.parent = null;
        this.element = null;
        this.attribute = null;
        this.class.$element = null;
        this.class.$parent = null;
        this.class.$attribute = null;
        this.class = null;

        // todo can I cache it here, and reuse?
    }



}
