import { Router } from '../utils/index';
import { EventAggregator } from '../utils/index';
import { customElement } from '../decorator/index';
import { inject } from '../decorator/index';
import { IElement } from '../interface/index';
import { ContainerClasses } from '../container/index';
import { ViewParser } from '../view/index';
import { ViewController } from '../view/index';



@customElement('mf-router')
@inject(EventAggregator, Router)
export class MfRouter implements IElement {

    // injected
    public router: Router;
    public eventAggregator: EventAggregator;

    // added by template engine
    public $element: Element;
    public $parent: IElement;
    public $view: ViewController;

    // current page instance
    private instance: IElement;

    // state
    private started: boolean;
    private loaded: boolean;



    constructor(eventAggregator: EventAggregator, router: Router) {
        // logger

        // set to class
        this.router = router;
        this.eventAggregator = eventAggregator;

        // register with router
        this.router.registerElement(this);
    }

    /**
     * created
     *
     */
    public created() {
        // nothing atm
    }



    /**
     * attached
     *
     */
    public attached() {
        // so we know...
        this.started = true;

        if (!this.loaded) {
            this.update();
        }

    }



    /**
     * detached
     *
     */
    public detached() {
        // if this happens, then lets send out one incase
        this.router.unregisterElement(this);
    }



    /**
     * loadTemplateAsync
     *
     */
    public async loadTemplateAsync() {

        // if router is not started then we wait until it calls us
        if (!this.started) {

            // you could have displayed something here...
            return '<template></template>';
        } else {

            // if route fails, then show error (good chance they pass in something we cant use)
            try {

                // get current route module
                const route = this.router.getCurrentModule();

                // todo, check for configured callback
                const template = await this.router.loadCurrentModule(); // <-  this need to be async..
                if (template === undefined) {
                    console.warn('route not found' + route);
                }
                const instance = ContainerClasses.get(template[this.router.getCurrentClass()]);
                this.instance = instance;

                // return template
                if (instance.loadTemplateAsync) {
                    return await instance.loadTemplateAsync();
                } else {
                    return instance.loadTemplate();
                }

            } catch (e) {
                return `<template>${e}</template>`;
            }

        }
    }



    /**
     * loadTemplate
     *
     */
    public loadTemplate() {

        // if router is not started then we wait until it calls us
        if (!this.started) {
            // OK
        } else {
            console.log('Im not supposed to trigger, BUG!!!');
        }

        return '<template></template>';
    }



    /**
     * update
     *
     */
    public update() {

        // vars
        const instanceOld = this.instance;
        let response = true;

        // if old, then check if we can deactivate
        if (instanceOld) {
            if (instanceOld.canDeactivate) {
                // switch to async ?
                response = instanceOld.canDeactivate();
            }
        }

        // TODO do we also want a canActivate?

        if (response) {

            // if we have old instance then we need to remove it;
            if (instanceOld) {

                if (instanceOld.detached) {
                    instanceOld.detached();
                }
                ViewParser.clearAllViews(instanceOld.$element);
                instanceOld.$element = null;
                instanceOld.$parent = null;
                instanceOld.$attributes = null;
                instanceOld.$element = null;
            }

            // remove children
            while (this.$element.firstChild) {
                this.$element.removeChild(this.$element.firstChild);
            }

            // create a view put route in
            const view = document.createElement('view');


            // load templates
            this.loadTemplateAsync().then((templateString) => {

                this.loaded = true;
                // module will be element in routes <view> tag
                ViewParser.parseAndCreateElement(this.instance, view, this, templateString);

                this.$element.appendChild(view);
                this.router.isNavigating = false;
                if (this.instance.activated) {
                    this.instance.activated();
                }

            });

        }


    }



}
