

/**
 * queue used internally so some parts get time to breath
 *
 */
export class Queue {
    public count = 0;
    public tasks: any[] = [];
    public topQueue: any[] = [];
    public started = false;
    private index = -1;



    /**
     * returns true/false depending if it s finished
     *
     */
    public finsished(): boolean {
        return this.count === 0;
    }



    /**
     * add task
     * todo: create interface
     *
     */
    public add(task: any): void {
        this.count++;
        this.tasks.push(task);
        if (!this.started) {
            this.started = true;
            setTimeout(() => {
                // requestAnimationFrame(() => {
                this.flush();
                //  });
            }, 0);
        }
    }



    /**
     * flushes the queue
     *
     */
    private flush(): void {
        if (this.index < 0) {
            if (this.tasks.length < 600) {
                this.topQueue = this.tasks.slice(0, this.tasks.length);
                this.tasks = [];
            } else {
                this.topQueue = this.tasks.slice(0, 600);
                const newAr = this.tasks.slice(600, this.tasks.length);
                this.tasks = newAr;
            }
            this.index = this.topQueue.length - 1;
            // this.topQueue.reverse();
        }
        if (this.topQueue.length === 0) {
            this.started = false;
        }

        this.runTasks();
    }



    /**
     * reflushes the queue (usually if there is more)
     *
     */
    private reFlush(): void {
        setTimeout(() => {
            //  requestAnimationFrame(() => {
            this.flush();
            // });
        }, 0);
    }



    /**
     * run the tasks
     *
     */
    private runTasks(): void {
        const curTask = this.topQueue[this.index];
        this.index--;
        let done: Function;
        done = () => {
            const nextTask = this.topQueue[this.index];
            this.index--;
            if (nextTask) {
                nextTask.call(done);
                this.count--;
            } else {
                if (this.index < 0) {
                    this.reFlush();
                } else {
                    this.started = false;
                }
            }
        };
        if (curTask) {
            curTask.call(done);
        }

        this.count--;
    }

}

