import { AttributeController } from './attributeController';
import { ElementController } from './elementController';
import { InterpolateController } from './interpolateController';
import { IControllerObject, INode } from '../interface/index';

/**
 * Holds all elements/attributes and interpolates, so we can clear them later
 *
 */
export class ViewController {
    private element: INode;
    private items: IControllerObject;
    private count = 0;


    constructor(element: INode) {
        this.element = element;
    }



    /**
     * adds to internal object so we can call detached later
     *
     */
    public addElement(_class: ElementController) {
        this.count++;
        if (!this.items) {
            this.items = {};
        }
        this.items['e' + this.count] = _class;
    }



    /**
     * adds to internal object so we can call detached later
     *
     */
    public addAttribute(_class: AttributeController): void {
        this.count++;
        if (!this.items) {
            this.items = {};
        }
        this.items['a' + this.count] = _class;
    }



    /**
     * adds to internal object so we can call detached later
     *
     */
    public addInterpolate(_class: InterpolateController): void {
        this.count++;
        if (!this.items) {
            this.items = {};
        }
        this.items['i' + this.count] = _class;
    }



    /**
     * returns current element
     *
     */
    public getElement(): INode {
        return this.element;
    }



    /**
     * calls detached on all attributes/elements and interpolates binded on view
     *
     */
    public clearView(): void {

        for (const item in this.items) {
            if (this.items[item].detached) {
                this.items[item].detached();
            }
        }


        this.items = null;
        this.element = null;

    }
}
