import { tokenize } from './tokenize';
import { generateAST } from './generateAst';
import { ClassPropertyObserverCreator } from './classPropertyObserverCreator';
import { Cache } from '../utils/index';
import { CONSTANTS } from '../interface/index';

const splitCache = {};


/**
 * ...todo
 *
 */
const contextOfObject = function (value: any, ctx: any) {

    let keys, key;
    if (!splitCache[value]) {
        keys = value.split(/\.|\[([^\]]+)\]\.?/).filter((x: any) => x ? x : null);
        key = keys[0];
        splitCache[value] = key;
    } else {
        key = splitCache[value];
    }


    // more then 1, then its array
    let newCtx = ctx;
    let overrideCtx: any;
    if (ctx.isMultiContext) {
        newCtx = ctx.ctx;
    }

    if (typeof key === 'string' && key[0] !== '$') {
        if (newCtx && newCtx.overrideContext) {
            while (newCtx.overrideContext) {
                newCtx = newCtx.overrideContext;
                if (key in newCtx) {
                    overrideCtx = newCtx;
                }
            }
        }
    }

    return overrideCtx || ctx;
};



/**
 * creates binding expression by getting tokens from the text
 *
 */
export function createBindingExpression(text: string, _class: any, setterClass: any) {

    let x: any;
    let nodeText: any;
    let attributes: any;
    let ast: any;


    if (text) {
        // todo, clean up
        // will I get memory leak here ?

        nodeText = text;
        // array to store attributes in
        attributes = [];

        if (Cache.expressionMapHas(nodeText)) {
            x = Cache.expressionMapGet(nodeText);
            attributes = (<any>x).attributes;
            ast = (<any>x).ast;

        } else {
            // do text have ${} ? if so its a mix
            // const isMix = nodeText.indexOf('${') !== -1 ? true : false;

            const tokens: any = tokenize(nodeText);
            ast = generateAST(tokens);

            // for each token get the attribute on variables
            tokens.forEach((tok: any) => {
                if (tok.type === 'variable') {


                    const newctx = contextOfObject(tok.value, _class);


                    if (typeof tok.value === 'string'
                        && tok.value[0] !== '$'
                        && newctx
                        && newctx.__proto__
                        && newctx.__proto__[CONSTANTS.META_COMPUTEDFROM]
                        && newctx.__proto__[CONSTANTS.META_COMPUTEDFROM][tok.value]) {
                        const computedFrom = newctx.__proto__[CONSTANTS.META_COMPUTEDFROM];
                        computedFrom[tok.value].attributes.forEach((val: any) => {
                            if (attributes.indexOf(val) === -1) {
                                attributes.push(val);
                            }

                        });
                    } else {
                        if (attributes.indexOf(tok.value) === -1) {
                            attributes.push(tok.value);
                        }
                    }
                }
            });

            Cache.expressionMapSet(nodeText, {
                attributes: attributes,
                ast: ast
            });
        }

        // set ast to setter
        setterClass.setAst(ast);

        // for each attribute found
        for (let i = 0; i < attributes.length; i++) {

            const newctx = contextOfObject(attributes[i], _class);
            ClassPropertyObserverCreator.create(newctx || _class, attributes[i], setterClass);
        }

        // initiate setter calss
        setterClass.init();
    }

}



/**
 * ...todo
 *
 */
export function removeBindingExpression(text: string, _class: any, handlerClass: any) {
    if (text) {
        if (Cache.expressionMapHas(text)) {
            const cachedExpression = Cache.expressionMapGet(text);
            const attributes = (<any>cachedExpression).attributes;
            for (let i = 0; i < attributes.length; i++) {
                const newctx = contextOfObject(attributes[i], _class);
                ClassPropertyObserverCreator.remove(newctx || _class, attributes[i], handlerClass);
            }
        }
    }
}
