import { customAttribute } from '../decorator/index';
import { IElement, IAttribute } from '../interface/index';
import { BindingEngine } from '../binding/index';


/**
 * simple attribute for misc types of events/attibutes
 *
 */
@customAttribute('href.bind')
@customAttribute('readonly.bind')
@customAttribute('disabled.bind')
@customAttribute('show.bind')
@customAttribute('class.bind')
export class MiscAttributes implements IAttribute {

    // public
    public $element: Element;
    public $attribute: Attr;
    public $parent: IElement;

    // private
    private subscribeInternal: any;
    private value: string;
    private init: boolean;
    private name: string;


    constructor() {
        // na
    }



    /**
     * created
     *
     */
    public created() {

        this.value = this.$attribute.value;
        this.name = this.$attribute.name.replace('.bind', '');
        this.init = true;
        this.subscribeInternal = {
            name: this.name + 'Attribute(misc)',
            value: this.value,
            call: (newValue: any, oldValue: any) => {
                if (oldValue !== newValue || this.init) {
                    this.init = false;
                    switch (this.name) {
                        case 'readonly':
                            if (newValue) {
                                (this.$element as any).readOnly = newValue;
                            } else {
                                this.$element.removeAttribute(this.name);
                            }
                            break;
                        case 'disabled':
                            if (newValue) {
                                (this.$element as any).disabled = newValue;
                            } else {
                                this.$element.removeAttribute(this.name);
                            }
                            break;
                        case 'class':
                            if (newValue) {
                                (this.$element as any).className = newValue;
                            } else {
                                (this.$element as any).className = '';
                            }
                            break;
                        case 'show':
                            if (newValue) {
                                // todo, remove or set block, check first ?
                                // or just use a class?
                                (this.$element as any).style['display'] = 'block';
                            } else {
                                (this.$element as any).style['display'] = 'none';
                            }
                            break;
                        default:
                            this.$element.setAttribute(this.name, newValue);
                    }
                }
            }
        };

        BindingEngine.subscribeClassProperty(this.$parent, this.value, this.subscribeInternal);
    }



    /**
     * detached
     *
     */
    public detached() {
        BindingEngine.unSubscribeClassProperty(this.$parent, this.subscribeInternal);
        // todo
    }



    /**
     * attached
     *
     */
    public attached() {
        // TODO: do I even need to do this, do I even wat to do this?
        this.$element.removeAttribute(this.$attribute.name);
    }



}
