import { CharCodes } from './charcode';

// create charCode class we can use everytime
const charCode = new CharCodes();

/**
 * Class for taking a expression and generating tokens for ast
 *
 */
export class Tokenizer {
    private expression: string;
    private baseTokens: any[] = [];
    private chars: any[] = [];
    private curChar: any = null;
    private curCharNo = 0;
    private curtype: string = null;
    private expressionLength: number;
    private isMix: boolean; // if its a mix of text and interpolate expressions
    private isOutsideExpression: boolean; // if mix stuff outside ${} is text only
    private expressionOriginal: string;



    constructor(expression: string) {
        this.expressionOriginal = expression;
        this.isMix = expression.indexOf('${') !== -1;
        this.isOutsideExpression = this.isMix;
        this.expression = this.trim(this.expressionOriginal);
        this.curtype = expression.indexOf('${') > 0 ? 'string' : null;
        this.curChar = this.expression.charCodeAt(this.curCharNo);
        this.expressionLength = this.expression.length;
    }


    /**
     * starts to parse and returns tokens
     *
     */
    public start(): any[] {
        this.generateBaseTokens();

        return this.baseTokens;
    }



    /**
     * trims down eveything thats not text so we dont need to check for whitespace later
     *
     */
    private trim(expression: string): string {
        let text = this.isOutsideExpression;
        let trimmed = '';
        for (let i = 0; i < expression.length; i++) {
            switch (true) {
                case text && charCode.DOLLAR.has(expression.charCodeAt(i)) && charCode.MUSTACHE_START.has(expression.charCodeAt(i + 1)):
                    trimmed = trimmed + expression[i];
                    text = false;
                    break;

                case !text && charCode.MUSTACHE_END.has(expression.charCodeAt(i)):
                    trimmed = trimmed + expression[i];
                    text = true;
                    break;

                case text && !charCode.STRING_START_END.has(expression.charCodeAt(i)):
                    trimmed = trimmed + expression[i];
                    break;

                case text && charCode.STRING_START_END.has(expression.charCodeAt(i)):
                    trimmed = trimmed + expression[i];
                    text = false;
                    break;

                case !text && charCode.STRING_START_END.has(expression.charCodeAt(i)):
                    trimmed = trimmed + expression[i];
                    text = true;
                    break;

                case !text && charCode.WHITESPACE.has(expression.charCodeAt(i)):
                    // skip only
                    break;

                case !text && !charCode.WHITESPACE.has(expression.charCodeAt(i)):
                    trimmed = trimmed + expression[i];
                    break;
            }
        }

        return trimmed;

    }



    /**
     * goes to next char in expression
     *
     */
    private advance(): void {
        this.curCharNo++;
        this.curChar = this.expression.charCodeAt(this.curCharNo);
    }



    /**
     * adds to token until it switched type/emds
     *
     */
    private getString(): void {
        let startend = charCode.STRING_START_END.has(this.curChar);
        let expressionStart = this.expressionStart();
        let done = this.parsedAllChars();

        while (!startend && !expressionStart && !done) {
            this.chars.push(this.curChar);
            this.advance();
            startend = charCode.STRING_START_END.has(this.curChar);
            expressionStart = this.expressionStart();
            done = this.parsedAllChars();
        }

        // do we have type? mixed expressions could have stared right on a expression
        if (this.curtype) {
            this.addToken();
        }


        if (!this.isOutsideExpression && startend && !expressionStart) {
            this.advance();
        }

    }



    /**
     * adds to token until it switched type/emds
     *
     */
    private getNumber(): void {
        let variable = charCode.REAL.has(this.curChar);
        let expressionStart = this.expressionStart();
        let done = this.parsedAllChars();

        while (variable && !expressionStart && !done) {
            this.chars.push(this.curChar);
            this.advance();
            variable = charCode.REAL.has(this.curChar);
            expressionStart = this.expressionStart();
            done = this.parsedAllChars();
        }
        this.addToken();
    }



    /**
     * checks if we are at the end of expression
     *
     */
    private parsedAllChars(): boolean {
        const done = this.curCharNo < this.expressionLength;

        return !done;
    }



    /**
     * adds to token until it switched type/emds
     *
     */
    private getVariable(): void {
        let variable = charCode.VARIABLE.has(this.curChar);
        let expressionStart = this.expressionStart();
        let done = this.parsedAllChars();

        while (variable && !expressionStart && !done) {
            this.chars.push(this.curChar);
            this.advance();
            variable = charCode.VARIABLE.has(this.curChar);
            expressionStart = this.expressionStart();
            done = this.parsedAllChars();
        }
        this.addToken();
    }



    /**
     * adds to token until it switched type/emds
     *
     */
    private getOperator(): void {
        let operator = charCode.OPERATOR.has(this.curChar);
        let expressionStart = this.expressionStart();
        let bracetsLeft = charCode.BRACKETLEFT.has(this.curChar);
        let bracetsRight = charCode.BRACKETRIGHT.has(this.curChar);
        let negativeNumber = this.negativeNumber();
        let done = this.parsedAllChars();

        if (bracetsLeft || bracetsRight) {
            this.chars.push(this.curChar);
            this.advance();
        } else {

            while (operator && !expressionStart && !done && !bracetsLeft && !bracetsRight && !negativeNumber) {
                this.chars.push(this.curChar);
                this.advance();
                operator = charCode.OPERATOR.has(this.curChar);
                bracetsLeft = charCode.BRACKETLEFT.has(this.curChar);
                bracetsRight = charCode.BRACKETRIGHT.has(this.curChar);
                expressionStart = this.expressionStart();
                negativeNumber = this.negativeNumber();
                done = this.parsedAllChars();
            }
        }

        // do we have anything ?
        if (this.chars.length > 0) {
            this.addToken();
        }
        if (negativeNumber) {
            this.curtype = null;
            this.chars.push(this.curChar);
            this.advance();
        }
    }



    /**
     * checks for interpolate expressions start
     *
     */
    private expressionStart(): boolean {
        const t1 = this.expression.charCodeAt(this.curCharNo);
        const t2 = this.expression.charCodeAt(this.curCharNo + 1);

        return charCode.DOLLAR.has(t1) && charCode.MUSTACHE_START.has(t2);
    }



    /**
     * checks if we have operator, plus or minus thena number... then its a negative number
     *
     */
    private negativeNumber(): boolean {
        const t1 = this.expression.charCodeAt(this.curCharNo - 1);
        const t2 = this.expression.charCodeAt(this.curCharNo);
        const t3 = this.expression.charCodeAt(this.curCharNo + 1);

        return charCode.OPERATOR.has(t1) && charCode.PLUS_OR_MINUS.has(t2) && charCode.NUMBERS.has(t3);
    }



    /**
     * adds token
     *
     */
    private addToken(): void {
        const val = this.chars.map(a => String.fromCharCode(a)).join('');
        this.baseTokens.push({
            type: this.curtype,
            value: this.curtype === 'number' ? parseFloat(val) : val
        });
        this.chars = [];
        this.curtype = null;
    }



    /**
     * loops expression and adds tokens
     *
     */
    private generateBaseTokens(): void {
        let done = this.parsedAllChars();

        while (!done) {
            switch (true) {

                case this.expressionStart():
                    this.isOutsideExpression = false;
                    this.curtype = 'operator';
                    this.chars.push(';'.charCodeAt(0));
                    this.addToken();
                    this.advance();
                    this.advance();
                    break;

                case this.isOutsideExpression:
                    this.getString();
                    break;

                case this.curtype === null && charCode.STRING_START_END.has(this.curChar):
                    this.curtype = 'string';
                    this.advance();
                    this.getString();
                    break;

                case this.curtype === null && charCode.MUSTACHE_END.has(this.curChar):
                    this.isOutsideExpression = true;
                    this.curtype = 'operator';
                    this.chars.push(';'.charCodeAt(0));
                    this.addToken();
                    this.curtype = 'string';
                    this.advance();
                    break;

                case this.curtype === null && charCode.NUMBERS.has(this.curChar):
                    this.curtype = 'number';
                    this.getNumber();
                    break;

                case this.curtype === null && charCode.VARIABLE.has(this.curChar):
                    this.curtype = 'variable';
                    this.getVariable();
                    break;

                case this.curtype === null && charCode.WHITESPACE.has(this.curChar):
                    // skip it, we dont want whitespce thats beetween operators and variables
                    this.advance();
                    break;

                case this.curtype === null && charCode.OPERATOR.has(this.curChar):
                    this.curtype = 'operator';
                    this.getOperator();
                    break;

                default:
                    console.warn('bug tokenizer', String.fromCharCode(this.curChar));
                    console.warn('bug tokenizer', this.expression);
                    this.advance();

            }
            done = this.parsedAllChars();
        }
    }

}
