import { customAttribute } from '../decorator/index';
import { IElement, IAttribute } from '../interface/index';
import { ViewController, ViewParser } from '../view/index';
import { BindingEngine } from '../binding/index';
import { IControllerArray } from '../interface/i_ControllerArray';



/**
 * removes dom when expression is undefined/null''0
 *
 */
@customAttribute('if.bind')
export class IfAttribute implements IAttribute {
    public $element: Node;
    public $attribute: Attr;
    public $parent: IElement;
    private value: any;
    private elementClone: Node;
    private anchor: Node;
    private added: boolean;
    private subscribeCall: any;
    private $view: any;

    constructor() {
        // nothing
    }



    /**
     * created
     *
     */
    public created() {

        // get current value;
        this.value = this.$attribute.value;

        // remove the if.bind attribute
        this.$element.attributes.removeNamedItem('if.bind');
        if (this.$element && (this.$element as any).$view) {
            this.$view = (this.$element as any).$view;
        }

        // clone node
        this.elementClone = this.$element.cloneNode(true);

        // create anchor so where know where to inject it
        this.anchor = document.createComment('mf-if-bind');
        if (this.$view) {
            (this.anchor as any).$view = this.$view;
        }

        // add anchor
        this.$element.parentNode.insertBefore(this.anchor, this.$element);

        // remove main element
        this.remove();

        // set variable for later
        this.added = false;


    }



    /**
     * attached
     *
     */
    public attached() {

        this.subscribeCall = {
            name: 'ifAttribute',
            value: this.value,
            call: (newValue: any/*, oldVal: any*/) => {

                switch (true) {
                    case newValue && !this.added:
                        this.added = true;
                        this.add();
                        break;
                    case !newValue && this.added:
                        this.remove();
                        break;
                    default:
                    // ?
                }
            }
        };


        BindingEngine.subscribeClassProperty(this.$parent, this.value, this.subscribeCall);
    }



    /**
     * detached
     *
     */
    public detached(): void {
        BindingEngine.unSubscribeClassProperty(this.$parent, this.subscribeCall);
    }



    /**
     * shows element/content
     *
     */
    public add() {
        // create clone
        const template = this.elementClone.cloneNode(true);

        // set clone as current element
        this.$element = template;
        (template as any).$view = new ViewController(template);

        // get attach event for elements
        let controllers: IControllerArray = [];
        controllers = ViewParser.parseNodes(this.$element, this.$parent, (template as any).$view);
        this.anchor.parentNode.insertBefore(this.$element, this.anchor);

        // if this even used after last rebuild?
        // todo - comment out do more test
        controllers.forEach((x) => {
            if (x.attached) {
                x.attached();
            }
        });
        controllers = null;


    }



    /**
     * removes content
     *
     */
    private remove() {
        let x = this.$element;
        if (this.added) {
            this.added = false;
            ViewParser.clearAllViews(this.$element);
            this.$element = null;
        }
        // what to do if there is no parent ?
        this.anchor.parentNode.removeChild(x);
        x = null;
    }



}
