import { BindingEngine } from '../binding/index';
import { INode } from '../interface/index';
import { ViewController } from './viewController';
import { Logger } from '../utils/index';

/**
 * internal subscribe caller of Interpolate controller
 *
 */
const SubscribeInternal = class {
    private firstRun = true;
    public name = 'Interpolate';
    public node: INode;

    constructor(node: INode) {
        this.node = node;
    }
    public call(newValue: any, oldValue: any) {
        if (oldValue !== newValue || this.firstRun) {
            this.firstRun = false;
            if ((this.node as any).nodeType === 2) {
                (this.node as Attr).value = newValue;
            } else {
                (this.node as Text).data = newValue;
            }
        }
    }
};



/**
 * Interpolate controller watches the text and updates node
 *
 */
export class InterpolateController {
    private parent: any;
    private subscribeInternal: any;
    private node: INode;
    private logger: Logger;
    private attr = false;


    constructor(parent: any, node: INode, $view: ViewController) {
        if (!(node as any).data) {
            this.attr = true;
        }
        this.logger = Logger.getLogger(this.attr ? (node as any).value.trim() : (node as any).data.trim(), 'interpolate');

        this.parent = parent;
        this.node = node;
        $view.addInterpolate(this);
    }



    /**
     * start element life cycle
     *
     */
    public init(): void {
        this.logger.log('init');
        this.subscribeInternal = new SubscribeInternal(this.node);
        BindingEngine.subscribeClassProperty(this.parent, this.attr ? (this.node as any).value : (this.node as any).data, this.subscribeInternal);
    }



    /**
     * attached... dont really do much atm... Todo, should I subscribe here ?
     *
     */
    public attached(): void {
        this.logger.log('attached');
        // na
    }



    /**
     * detached, unsubscribes and clear out
     *
     */
    public detached(): void {
        this.logger.log('detached');
        BindingEngine.unSubscribeClassProperty(this.parent, this.subscribeInternal);
        this.subscribeInternal = null;
        this.parent = null;
        this.node = null;
    }



}
